.bg-video {
  position: fixed;
  top: 0;
  left: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-form {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 300px;
  border-radius: 12px;
  text-align: center;
}

.glass-card {
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  color: #fff;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.1);
}

.login-title {
  color: #00ffff;
  font-size: 1.5rem;
}

input {
  padding: 10px;
  border-radius: 6px;
  border: none;
}

button {
  padding: 10px;
  border-radius: 6px;
  background: #00ffff;
  color: #000;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

.error-text {
  color: red;
  font-size: 0.9rem;
}
.glass-blur {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
  color: #fff;
}
.input-group {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 1.5rem; /* ✅ adds space between inputs */
}

.input-group input {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 2.5rem;
  border: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  outline: none;
  transition: box-shadow 0.3s ease, background 0.3s ease;
}

.input-group input:focus,
.input-group input:hover {
  box-shadow: 0 0 10px 2px rgba(0, 255, 255, 0.6); /* ✅ glowing effect */
  background: rgba(255, 255, 255, 0.15);
}

.login-button {
  width: 100%;
  padding: 0.75rem;
  margin-top: 1rem;
  border: none;
  border-radius: 10px;
  background: linear-gradient(135deg, #00f0ff, #0077ff);
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: box-shadow 0.3s ease;
}

.login-button:hover {
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.8); /* ✅ glowing button */
}
