.login-success-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: rgba(15, 15, 25, 0.7);
  color: #e0e0e0;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  box-shadow: 0 0 24px rgba(0, 255, 255, 0.1);
}

h2, h3 {
  color: #00ffff;
  text-align: center;
}

.community-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin: 1rem 0;
}

.community-buttons button {
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  background: #00ffff33;
  border: none;
  color: #00ffff;
  cursor: pointer;
}

.community-buttons button:hover {
  background: #00ffff55;
}

.peer-list, .notifications {
  margin-top: 2rem;
}

.peer-card {
  display: flex;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.peer-card button {
  background: #00ffffaa;
  border: none;
  padding: 0.4rem 1rem;
  border-radius: 6px;
  color: #101010;
  font-weight: bold;
  cursor: pointer;
}

.peer-card button:disabled {
  background: #aaa;
  color: #333;
  cursor: not-allowed;
}
